body {
  background-image: url("https://yt3.ggpht.com/ytc/AKedOLSqArGVW9UO1kWLQmV4cy5gkGBKRN0w_Ez3WYYK=s900-c-k-c0x00ffffff-no-rj");
  background-size: cover;
  background-position: top;
  padding: 3rem;
}
.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 95vh;
  position: relative;
}
p {
  color: #fff;
}

.App h1 {
  margin-top: 4rem;
  color: #ff6900;
  font-size: 2.75rem;
  font-style: italic;
}

p {
  font-size: 1.5rem;
  color: rgb(255, 251, 251);
  margin-bottom: 0;
}

footer p {
  color: rgb(185, 181, 181);
  font-size: 1rem;
  margin-top: 0;
}

@media (max-width: 896px) {
  body {
    margin: 2px 0;
    background-size: cover;
    background-position: bottom;
    background-repeat: no-repeat;
  }

  header{
    margin-top: -4rem;
  }


  .App h1 {
    font-size: 1.5rem;
    padding: auto 0;
  }

  .App p {
    font-size: 0.75rem;
  }

  /* to center a positioned tag */
  footer p {
    margin: 1rem 0;
    position: absolute;
    bottom: 2rem;
    left: 0;
    right: 0;
  }
}

@media (max-width: 480px) {
  .App h1 {
    font-size: 1.5rem;
  }

  .App h2 {
    font-size: 0.85rem;
  }

  footer p {
    font-size: 1rem;
  }
}
